.header{
    background-color: #131921;
    display: flex;
    width: 100%;
    align-items: center;
    position: sticky; top: 0;
    z-index: 200;
}
.header_logo{
    width: 100px;margin: 0 20px;
    margin-top: 18px;
    object-fit: contain;
}

.header__optionBasket{
    display: flex;
    align-items: center;
}
.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
}
.header__nav{
    display:flex;
    justify-content: space-evenly;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    margin-left: 10px;
}


.header__optionLineOne{
   font-size: 10px;
}
.header__optionLineTwo{
  font-size: 13px; font-weight: 800; 
}
.header__link{
    color: white;
    text-decoration: none;
}
.header__search{
   display: flex;
   flex: 1;
}


.header__searchInput{
 height: 12px;
 padding: 10px;
 border: none;width:100%;
}

.header__searchIcon{
    padding: 5px; height: 22px !important;
    background-color: #cd9042;
}